html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
*::selection {
  background: rgba(255, 204, 24, 0.6);
  /* Safari */
}
*::-moz-selection {
  background: rgba(255, 204, 24, 0.6);
  /* Firefox */
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
body {
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: normal;
  line-height: 1.25;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.list-reset {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.container {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
}
.promo__subtitle {
  font-size: 26px;
  color: #484644;
}
.promo__title {
  width: 100%;
  padding-left: 25%;
  display: block;
  color: #484644;
  font-size: 30px;
  margin-bottom: 15px;
  margin-top: 0;
  padding-top: 20px;
}
@media (max-width: 600px) {
  .promo__title {
    padding-left: 0;
  }
}
.page-title {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
  background-color: white;
  position: relative;
  z-index: 2;
}
.page-title__figure {
  width: 100%;
  position: relative;
  margin: 0;
  background: url(../img/background.png) no-repeat 50% 100% #484644;
  background-size: auto;
  background-size: cover;
}
.page-title__figure::before {
  content: "сделано в России";
  padding: 2px 4px;
  display: inline-block;
  background: #fe2512;
  color: white;
  font-size: 20px;
  letter-spacing: 0.08em;
  text-align: center;
  position: absolute;
  right: 10px;
  top: 10px;
  font-weight: 600;
  text-transform: uppercase;
}
@media (max-width: 500px) {
  .page-title__figure::before {
    font-size: 16px;
  }
}
.page-title__figure::after {
  content: "";
  width: 100px;
  height: 100px;
  position: absolute;
  bottom: 20px;
  right: 10px;
  background-image: url(../img/marca1.png);
  background-size: cover;
  background-repeat: no-repeat;
}
.page-title__img {
  filter: drop-shadow(-6px 6px 8px #222);
  padding: 17% 30% 2% 5%;
  width: 100%;
}
.page-title__text {
  margin-top: 20px;
  display: block;
}
.privilege {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
}
.privilege__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.privilege__item {
  width: 50%;
  display: flex;
  align-items: center;
  padding-right: 15px;
}
@media (max-width: 500px) {
  .privilege__item {
    width: 100%;
  }
}
.privilege__img {
  width: 100px;
}
.privilege__text {
  padding-left: 10px;
  width: calc(100% - 100px);
}
.tech-param {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
  display: flex;
  flex-wrap: wrap;
}
.tech-param__title {
  width: 100%;
}
.tech-param__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
  width: 100%;
}
.tech-param__item {
  width: 49%;
  background-color: rgba(255, 204, 24, 0.5);
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
}
@media (max-width: 600px) {
  .tech-param__item {
    width: 100%;
  }
}
.tech-param__item .tech-param__value {
  font-weight: bold;
}
.goods {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
}
.goods__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}
.goods__item {
  width: 32%;
  margin-right: 2%;
  margin-bottom: 2%;
  padding: 30px;
  background: rgba(72, 70, 68, 0.1);
  position: relative;
}
.goods__item:nth-child(3n) {
  margin-right: 0;
}
.goods__item:hover {
  box-shadow: 0 5px 7px rgba(0, 0, 0, 0.3);
  bottom: 5px;
  cursor: pointer;
  background-color: rgba(255, 204, 24, 0.5);
  transition: 0.8s ease;
}
@media (max-width: 700px) {
  .goods__item {
    width: 49%;
    margin-right: 2%;
    padding: 20px;
  }
  .goods__item:nth-child(3n) {
    margin-right: 2%;
  }
  .goods__item:nth-child(2n) {
    margin-right: 0;
  }
}
@media (max-width: 400px) {
  .goods__item {
    width: 100%;
    margin-right: 0;
  }
  .goods__item:nth-child(3n) {
    margin-right: 0;
  }
}
.goods__img-wrapper {
  width: 100%;
  height: 75%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 700px) {
  .goods__img-wrapper {
    height: 80%;
  }
}
@media (max-width: 400px) {
  .goods__img-wrapper {
    height: 85%;
  }
}
.goods__img {
  width: 100%;
  height: auto;
  padding: 20px;
}
@media (max-width: 700px) {
  .goods__img {
    padding: 5px;
  }
}
@media (max-width: 400px) {
  .goods__img {
    padding: 10px;
  }
}
.goods__title {
  text-align: center;
  line-height: 1.3em;
  font-size: 16px;
  display: block;
  width: 100%;
}
.goods__link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}
.documentation {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
}
.documentation__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}
.documentation__item {
  width: 23.5%;
  margin-right: 2%;
  margin-bottom: 2%;
  background: rgba(72, 70, 68, 0.07);
  position: relative;
  padding: 20px;
}
.documentation__item:hover {
  box-shadow: 0 5px 7px rgba(0, 0, 0, 0.3);
  bottom: 5px;
  transition: 0.3s ease;
  cursor: pointer;
}
.documentation__item:nth-child(4n) {
  margin-right: 0;
}
@media (max-width: 650px) {
  .documentation__item {
    width: 49%;
  }
  .documentation__item:nth-child(2n) {
    margin-right: 0;
  }
}
.documenttion__link {
  color: black;
}
.documenttion__img {
  width: 60%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.documenttion__name {
  width: 80%;
  display: block;
  text-align: center;
  margin-top: 10px;
  margin-left: 10%;
  margin-right: 10%;
}
.feature {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
}
.feature__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}
.feature__item {
  width: 23.5%;
  margin-right: 2%;
  margin-bottom: 4%;
}
.feature__item:nth-child(4n) {
  margin-right: 0;
}
@media (max-width: 750px) {
  .feature__item {
    width: 32%;
  }
  .feature__item:nth-child(4n) {
    margin-right: 2%;
  }
  .feature__item:nth-child(3n) {
    margin-right: 0;
  }
}
@media (max-width: 600px) {
  .feature__item {
    width: 48%;
    margin-right: 2%;
  }
  .feature__item:nth-child(4n) {
    margin-right: 0;
  }
  .feature__item:nth-child(3n) {
    margin-right: 2%;
  }
  .feature__item:nth-child(2n) {
    margin-right: 0;
  }
}
@media (max-width: 400px) {
  .feature__item {
    width: 100%;
    margin-right: 0;
  }
  .feature__item:nth-child(3n) {
    margin-right: 0;
  }
}
.feature__img {
  display: block;
  width: 100%;
  border: 1px solid #484644;
}
.feature__text {
  width: 100%;
  display: block;
  margin-top: 15px;
}
.video {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
  display: flex;
  flex-wrap: wrap;
}
.video__title {
  width: 100%;
}
.video__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  width: 100%;
}
.video__item {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
@media (max-width: 700px) {
  .video__item {
    margin-bottom: 30px;
  }
}
@media (max-width: 700px) {
  .video__item {
    margin-bottom: 0;
  }
}
.video__iframe {
  width: 60%;
  padding-right: 10px;
  height: 300px;
  border: none;
}
@media (max-width: 700px) {
  .video__iframe {
    width: 100%;
    margin-bottom: 10px;
  }
}
.video__text {
  width: 40%;
  padding-left: 10px;
}
@media (max-width: 700px) {
  .video__text {
    width: 100%;
  }
}
@media (max-width: 500px) {
  .video__text {
    display: none;
  }
}
.promo-section {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
  padding-top: 30px;
}
.promo-section__figure {
  background-image: url(../img/background.png);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: 100% 200px;
  margin: 0;
}
.promo-section__img {
  width: 50%;
  margin: 7% 5% 0 5%;
}
.page-subnavigation {
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;
}
.page-subnavigation__wrapper {
  max-width: 959px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px;
}
@media (max-width: 959px) {
  .page-subnavigation__wrapper {
    display: flex;
    align-items: flex-start;
  }
}
.page-subnavigation__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  width: 100%;
  justify-content: space-between;
  background-color: #ffcc18;
}
@media (max-width: 959px) {
  .page-subnavigation__list {
    display: none;
  }
}
.page-subnavigation__list--active {
  display: block;
  width: 300px;
}
.page-subnavigation__item {
  flex-grow: 1;
}
.page-subnavigation__link {
  display: block;
  padding: 10px 0;
  color: #484644;
  text-align: center;
  border-right: 1px solid white;
}
.page-subnavigation__link:hover {
  background-color: #f2ab26;
  color: white;
}
.page-subnavigation__list--active .page-subnavigation__link {
  border-right: none;
}
.page-subnavigation__toogle {
  border: none;
  background-color: #ffcc18;
  margin-top: 0;
  display: none;
  padding: 10px;
  color: #484644;
  align-items: center;
}
.page-subnavigation__toogle::before {
  content: "";
  width: 15px;
  height: 15px;
  background-image: url("../img/icon-menu.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.page-subnavigation__toogle:hover {
  background-color: #f2ab26;
  cursor: pointer;
  color: white;
}
@media (max-width: 959px) {
  .page-subnavigation__toogle {
    display: flex;
  }
}
.page-subnavigation__toogle--menu-open {
  margin-right: 10px;
}
.page-subnavigation__toogle--menu-open::before {
  background-image: url("../img/icon-cross.svg");
}
.page-subnavigation__toogle-text {
  padding-left: 5px;
}
